<script>
import Cartype from '../cartype'
import SearchInfoInput from './infoInput.vue'
export default {
    components: {
        Cartype,
        SearchInfoInput,
    },
    name: 'SearchInfo',
    data() {
        return {
            url: 0,
            openFrom: true,
            textarea:'',
            collect: false,
            srcList: ['car-1.jpg','car-2.jpg','car-3.jpg','car-1.jpg','car-2.jpg','car-3.jpg','car-1.jpg','car-2.jpg','car-3.jpg'],
            imgListMove: 0,
            moveFrequency: 0,
            activeName: 'carinfo'

        }
    },
    methods:{
        getImg(e){
            this.$refs.carousel.setActiveItem(e)
        },
        rightMove() {
            if(this.srcList.length  > 5 && this.srcList.length - 5 - this.moveFrequency > 0) {
                this.imgListMove =  this.imgListMove - 165
                this.moveFrequency = this.moveFrequency + 1
                this.url = this.url + 1
            }
        },
        leftMove() {
            if( this.moveFrequency >= 1) {
                this.imgListMove =  this.imgListMove + 165
                this.moveFrequency = this.moveFrequency - 1
                this.url = this.url -1
            }
        },
        openFromBtn() {
            this.openFrom = !this.openFrom
        },
        collectBtn() {
            this.collect = !this.collect
        }
    }
    

}
