<template>
    <div class="search-input-page">
        <div class="search-form">
            <div class="title">
                <div class="icon"><i class="el-icon-shopping-cart-2"></i><br>BUY</div>
                <p>我要購車</p>
            </div>
            <div class="select">
                <p>會員姓名</p>
                <el-input v-model="name" placeholder="请输入内容"></el-input>
                <p>連絡電話</p>
                <el-input v-model="phone" placeholder="请输入内容"></el-input>
                <p>電子信箱</p>
                <el-input v-model="mail" placeholder="请输入内容"></el-input>
                <p>需求服務</p>
                <el-select v-model="value" filterable placeholder="請選擇">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <p>訊息內容</p>
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 4, maxRows: 10}"
                    placeholder="请输入内容"
                    v-model="message">
                </el-input>
            </div>
            <div class="search-btn" >
                <div class="car-btn" >確定送出</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'searchInput',
    data() {
        return {
            name:'',
            phone:'',
            mail:'',
            message:'',
            options: [{
                value: '1',
                label: '需求1'
                }, {
                value: '2',
                label: '需求2'
                }
            ],
            value: 1,
        }
    },
    methods: {
    }

}
</script>

<style lang="scss" scoped>
.search-input-page {
    .title {
        display: flex;
        color: #fff;
        height: 60px;
        text-align: center;
        align-items: center;
        .icon {
            background: #b04b23;
            padding: 10px;
            font-size: 12px;
            i {
                font-size: 24px;
                margin-bottom: 2px;
            }
        }
        p {
            background: #ca582a;
            text-align: center;
            width: 100%;
            line-height: 60px;
        }
    }
    .search-form {
        background: #f5f5f5;
        font-size: 14px;
        .title {
            .icon {
                background: #ef6d38;
            }
            p {
                background: #444444;
            }
        }
        .select {
            padding: 10px;
            color: #444444;
            p {
                margin: 25px 0 8px 25px;
            }
            
        }
        @media screen and (max-width: 768px){ 
            height: auto;
        }   
    }
    .search-btn {
        background: #f5f5f5;
        padding: 10px;
        .car-btn{
            font-size: 14px;
            width: 100%;
            height: 45px;
            line-height: 45px;
            margin:10px auto;
            border-radius: 50px;
        } 
    }
}
.search-form::-webkit-scrollbar-track
{
	background-color: #333333;
}

.search-form::-webkit-scrollbar
{
	width: 10px;
	background-color: #333333;
}

.search-form::-webkit-scrollbar-thumb
{
	background-color: #ef6d38;
}
</style>


